<template>
  <v-autocomplete 
  v-bind="$attrs" 
  :value="value"
  :search-input.sync="search"
  @input="emit"
  :items="dataItems"
  :loading="loading"
   placeholder="Start typing to Search"
        clearable
        return-object
  >
  </v-autocomplete>
</template>
<script>
import debounce from 'debounce'
import { mapActions } from 'vuex'

export default {
  name: 'autocomplete',
  props: {
    value: String,
    collection: {
      type: String
    }
  },
  data() {
    return {
      search: '',
      dataItems: [],
      loading: false
    }
  },
  methods: {
    emit (val) {
      this.$emit('input', val[this.$attrs.itemValue])
    },
    ...mapActions({
      get: 'api/get'
    }),
    async makeSearch (value, collection) {
      this.loading = true
      const res = await this.get({
        collection: collection,
        search: { name: value }
      })
   
      this.dataItems = res.data.docs
      this.loading = false

    }
  },
  watch: {
    search ( value ) {
      if(this.collection)
        debounce(this.makeSearch, 500)(value, this.collection)
    }
  },
  created () {
    
      if(this.collection) 
        this.makeSearch('', this.collection)
      else if (this.$attrs.items)
        this.dataItems = this.$attrs.items
 
      
  }
}
</script>